.Buyer-Dash2{
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #333;
}
.BuyerDash2-header{
  height: 100%;
  width: 100%;
  padding-left: 12vmin;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: white;
}
.BuyerDash2-nav{
  top: 0;
  left: 0;
  position: fixed;
  height: 100%;
  width: 5%;
  background-color: #333;
  display: flex;
  flex-direction: column;
  padding-top: 10vmin;
  align-items: center;
  justify-content: flex-start;
}
.Buyer2-row{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.Buyer2-column{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.BuyerUser2-container{
  height: 17vmin;
  width: 88%;
  margin: 1vmin;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.BuyerDash2-title{
  font-size: 4.5vmin;
  font-weight: bold;
  color: #333333;
}
.BuyerDash2-user{
  height: 10vmin;
  width: 30vmin;
  font-size: 2.5vmin;
  font-weight: bold;
  color: #333333;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Buyer2-icon{
  height: 7vmin;
  margin: 1vmin;
}
.BuyerStats2-container{
  background-color: white;
  border-width: 0.2px;
  border-style: solid;
  border-radius: 2vmin;
  border-color: rgba(0, 0, 0, 0.4);
  height: 25vmin;
  width: 87%;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: flex-start;
  justify-content: center;
  margin-left: 11vmin;
  padding: 2vmin;
  margin-bottom: 4vmin;
}
.BuyerPiechart2-container{
  height: 100%;
  width: 35%;
  margin: 1vmin;
  padding: 1vmin;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.BuyerCrop2-button{
  height: 4vmin;
  width: 25vmin;
  background-color: #26D7AB;
  border-width: 0.2vmin;
  border-style: solid;
  border-color: transparent;
  color: #fff;
  font-size: 2vmin;
  margin: 1vmin;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5vmin;
  text-decoration: none;
  padding: 0.5vmin;
}
.BuyerCrop2-button:hover{
  background-color: #fff;
  color: #26D7AB;
  border-color: #26D7AB;
  border-width: 0.2vmin;
  border-style: solid;
}

.FarmerDetails-container{
  background-color: white;
  border-width: 0.2px;
  border-style: solid;
  border-radius: 5vmin;
  border-color: rgba(0, 0, 0, 0.4);
  height: 18vmin;
  width: 75%;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: flex-start;
  justify-content: center;
  margin-left: 15vmin;
  padding: 2vmin;
  margin-top: 2vmin;
}

.Graphs{
  height: 35vmin;
  width: 50%;
  border-radius: 5vmin;
  background-color: #fff;
  border-width: 0.2vmin;
  border-style: solid;
  border-color: #fff;
  margin: 2vmin;
  margin-left: -5vmin;
  padding: 1vmin;
}
.Graph-Container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #000;
  font-weight: 500;
  font-size: 1.5vmin;
  overflow: visible;
}
.Graph-Container:hover{
  transform: scale(1.02);
}